import { formatTwoDigits } from './useFormatNumber'

// 取得目前日期時間的字串 (yyyy-mm-dd hh:mm:ss)
export default function() {
    let now = new Date();
    let year = now.getFullYear();
    let month = formatTwoDigits( now.getMonth() + 1 );
    let day = formatTwoDigits( now.getDate() );
    let hour = formatTwoDigits( now.getHours() );     // 0-23
    let minute = formatTwoDigits( now.getMinutes() ); // 0-59
    let second = formatTwoDigits( now.getSeconds() ); // 0-59

    return `${year}-${month}-${day} ${hour}:${minute}:${second}`;
}