<template>

    <div class="container">
        <div class="row">
            <div class="col-12">
                
                <section class="working-hours-calculation">
        
                    <!-- 已複製的提示訊息框 -->
                    <WorkingHoursCalculationNotice
                        :showCopyed="data.showCopyed" 
                    />

                    <!-- Header -->
                    <WorkingHoursCalculationHeader
                        ref="headerRef"
                        :data="data"
                        @eventWorkRecord="workRecord"
                        @eventCopyTableData="copyTableData"
                    />

                    <!-- Body -->
                    <WorkingHoursCalculationBody
                        ref="bodyRef"
                        :data="data"
                        @eventEnableComment="enableComment"
                        @eventRemoveRecord="removeRecord"
                        @eventCopyTrData="copyTrData"
                    />


                </section>

            </div>
        </div>
    </div>


    
</template>
<script>
import { ref, reactive, onMounted, nextTick } from 'vue'

// Adding Bootstrap 5 tooltip to Vue 3
// https://stackoverflow.com/questions/69053972/adding-bootstrap-5-tooltip-to-vue-3
import { Tooltip } from 'bootstrap'

// hooks
import { formatTwoDigits } from '@/hooks/useFormatNumber'
import useFormatNumber from '@/hooks/useGetNowDateTimeString'
import useCopySelectedData from '@/hooks/useCopySelectedData'

// components
import WorkingHoursCalculationNotice from './WorkingHoursCalculationNotice.vue'
import WorkingHoursCalculationHeader from './WorkingHoursCalculationHeader.vue'
import WorkingHoursCalculationBody from './WorkingHoursCalculationBody.vue'


export default {
    name: 'WorkingHoursCalculation',
    components: {
        WorkingHoursCalculationNotice,
        WorkingHoursCalculationHeader,
        WorkingHoursCalculationBody,
    },

    setup() {
        // 數據
        let data = reactive({
            record_arr: [], // 記錄陣列
            timer: null, // 計時器
            timer_text: '00:00:00', // 計時器文字
            timer_btn_type: 'start', // 計時按鈕類型: 'start' | 'end'
            timer_btn_text: 'Start', // 計時按鈕文字: 'Start' | 'Stop'
            hours_magic: false, // 時數無條件進位開關
            alert_sound: false, // 提示音開關
            showCopyed: false, // 複製完成提醒訊息
        })

        // 靜態元素
        let headerRef = ref(null)  // WorkingHoursCalculationHeader 子組件
        let bodyRef = ref(null)  // WorkingHoursCalculationBody 子組件



        onMounted(()=>{

            // init bs5 tooltips
            new Tooltip(document.body, {
                selector: "[data-bs-toggle='tooltip']",
            })

            nextTick(()=>{
                // do stuff with the DOM

            })
        })



        // 方法


        // 計時功能 (開始 | 暫停)
        function workRecord() {
            let now_str = useFormatNumber();
            let type = data.timer_btn_type;

            switch(type) {
                // 開始
                case 'start':                
                    // 更新計時器文字
                    data.timer_text = '00:00:01';
                
                    // 每秒執行
                    data.timer = setInterval(() => {
                        let start = new Date(now_str);
                        let now = new Date();
                        let offsetTime = Math.ceil( (now.getTime() - start.getTime() ) / 1000 );

                        // let day = formatTwoDigits( Math.floor(offsetTime / 60 / 60 / 24) );
                        let hour = formatTwoDigits( Math.floor(offsetTime / 60 / 60) % 24 );
                        let minute = formatTwoDigits( Math.floor(offsetTime / 60) % 60 );
                        let second = formatTwoDigits( Math.floor(offsetTime) % 60 );

                        // 更新計時器文字
                        data.timer_text = `${hour}:${minute}:${second}`;
                        console.log(data.timer_text);

                        // 整點提示音
                        if ( !data.alert_sound ) return;
                        if ( minute == '00' && second == '00' ) {
                            headerRef.value.alertPlayer.play(); // 取用子組件中的ref - 播放音檔
                            console.log('整點提示音');
                        }

                    }, 1000);
                
                    // 更新按鈕資訊
                    data.timer_btn_text = 'Stop';
                    data.timer_btn_type = 'end';

                    // 將 新記錄 加入陣列
                    data.record_arr.push({
                        r_id: data.record_arr.length,      // 0
                        r_start: now_str,   // '2024-01-01 10:00'
                        r_end: undefined,     // '2024-01-01 17:00'
                        r_comment: undefined, // '備註欄位'
                    
                        del_btn_enable: true, // 刪除紀錄按紐
                        comment_editable: false, // 備註欄位
                    });

                    break;
                
                // 結束
                case 'end':
                    // 清除計時器
                    clearInterval(data.timer);
                    data.timer = null;

                    // 更新計時器文字
                    data.timer_text = '00:00:00';
                
                    // 更新按鈕資訊
                    data.timer_btn_text = 'Start';
                    data.timer_btn_type = 'start';

                    // 更新最後一筆紀錄
                    data.record_arr[data.record_arr.length - 1].r_end = now_str;
                    data.record_arr[data.record_arr.length - 1].del_btn_enable = false;

                    break;
            }
        }

        // 刪除紀錄
        function removeRecord(id) {
            let flag = window.confirm(`確定要刪除此筆紀錄嗎？`);
            if ( !flag ) return;
            data.record_arr = data.record_arr.filter((item)=> item.r_id != id );
        }

        // 開啟 | 關閉 備註欄位
        function enableComment(id, flag) {
            for (let r of data.record_arr ) {
                if ( r.r_id == id ) {
                    r.comment_editable = flag; // 更新資料
                }
            }
        }

        // 複製表格資料
        function copyTableData() {

            // 複製
            useCopySelectedData(bodyRef.value.recordTable);

            // 更新資料
            data.showCopyed = true;
            setTimeout(()=>{
                data.showCopyed = false;
            }, 1000);
        }

        // 複製資料
        function copyTrData(e) {
            let tr = e.target.parentNode.parentNode;
            
            // 製作結構
            let clone_tr = tr.cloneNode(true); // clone tr
            let el = document.createElement('table');
            el.append(clone_tr);
            
            document.body.append(el); // 插入頁面
            useCopySelectedData(el); // 複製

            // 更新資料
            data.showCopyed = true;
            setTimeout(()=>{
                data.showCopyed = false;
            }, 1000);

            el.remove(); // 從頁面移除
        }

        



        return {
            // 數據
            data,

            // 元素
            headerRef,
            bodyRef,

            // 方法
            workRecord,
            removeRecord,
            enableComment,
            copyTableData,
            copyTrData,
        }
    }
}
</script>
