<template>

    <section>

        <div class="container">
            <div class="row">
                <div class="col-12">
                    <div class="upper-inner">

                        <!-- 計時器 -->
                        <div id="js-timer" class="timer-wrap">
                            <span class="timer-text">{{ initData.timer_text }}</span>
                        </div>

                        <div class="btn-wrap">
                            <!-- 計時按鈕 -->
                            <button :data-type="initData.timer_btn_type" @click="workRecord()" class="text-btn">{{ initData.timer_btn_text }}</button>

                            <!-- 複製按鈕 -->
                            <button @click="copyTableData()" class="icon-btn copy-all" data-bs-container="body" data-bs-toggle="tooltip" data-bs-placement="right" title="複製表格到剪貼簿"></button>
                        </div>

                        <div class="group-wrap">
                            <fieldset class="form-group">
                                <input type="checkbox" name="chk-alert-sound" id="chk-alert-sound" v-model="initData.alert_sound" @change="controlPlayer()">
                                <label for="chk-alert-sound">布穀提示音 (每小時播放)</label>
                                <button @click="playSound()" class="icon-btn play" data-bs-container="body" data-bs-toggle="tooltip" data-bs-placement="top" title="試聽"></button>
                                <button @click="pauseSound()" class="icon-btn pause" data-bs-container="body" data-bs-toggle="tooltip" data-bs-placement="top" title="停止播放"></button>
                                
                                <!-- audio -->
                                <audio loop preload="auto" ref="alertPlayer" class="audio-player">
                                    <source src="../../../assets/audio/cuckoo-clock-trimmed.mp3" type="audio/mpeg">
                                </audio>
                            </fieldset>

                            <fieldset class="form-group">
                                <input type="checkbox" name="chk-hour" id="chk-hour" v-model="initData.hours_magic">
                                <label for="chk-hour">時數進位為0.25的倍數 (灌水10分鐘以內)</label>
                            </fieldset>
                        </div>

                    </div>
                </div>
            </div>
        </div>

    </section>

</template>

<script>
import { ref, toRef } from 'vue';


export default {
    name: 'WorkingHoursCalculationHeader',
    props: ['data'],
    emits: [
        'eventWorkRecord',
        'eventCopyTableData',
    ],
    
    setup(props, context) {
        // props 的資料
        let initData = toRef(props, 'data')

        // 元素
        let alertPlayer = ref(null)


        // 方法

        function workRecord() {
            context.emit('eventWorkRecord')
        }

        function copyTableData() {
            context.emit('eventCopyTableData')
        }



        // 由 Client 端觸發 audio 操控 (不然不能自動播放音檔)
        function controlPlayer() {
            if ( !initData.value.alert_sound ) return;
            alertPlayer.value.pause();
            console.log('打勾');
        }

        // 試聽音檔
        function playSound() {
            alertPlayer.value.play();
            console.log('試聽提示音');
        }

        // 停止播放
        function pauseSound() {
            alertPlayer.value.pause();
            console.log('停止播放提示音');
        }


        return {
            initData,
            alertPlayer,

            workRecord,
            copyTableData,
            
            controlPlayer,
            playSound,
            pauseSound,
        }
    }
}
</script>