<template>

    <section>

        <div class="container">
            <div class="row">
                <div class="col-12">
                    <div class="container-inner">

                        <!-- 表格 -->
                        <div class="table-wrap">
                            <!-- 表格標題 -->
                            <table class="table-header table table-hover" width="100%">
                                <thead>
                                    <th>日期</th>
                                    <th>時段</th>
                                    <th>時數</th>
                                    <th>備註</th>
                                    <th>操作</th>
                                </thead>
                            </table>
                            <!-- 表格內容 (供複製) -->
                            <table class="table-body table table-hover" ref="recordTable" width="100%">
                                <tbody>

                                    <tr v-for="(item, index) of initData.record_arr" :key="index">
                                        
                                        <!-- 日期 -->
                                        <td>{{ formatTableData(item, 'start-date') }}</td>

                                        <!-- 時段 -->
                                        <td>{{ formatTableData(item, 'start-time') }}~{{ formatTableData(item, 'end-time') }}</td>

                                        <!-- 時數 -->
                                        <td>
                                            <!-- 魔法時數: 無條件進位值 -->
                                            <span v-if="initData.hours_magic">{{ formatTableData(item, 'magic-hours') }}</span>

                                            <!-- 真實時數: 原始值 -->
                                            <span v-if="!initData.hours_magic">{{ formatTableData(item, 'real-hours') }}</span>
                                        </td>

                                        <!-- 備註 -->
                                        <td>
                                            <!-- 顯示文字 -->
                                            <span v-if="!item.comment_editable" class="comment-text">{{ item.r_comment }}</span>
                                            
                                            <!-- 輸入框 -->
                                            <input type="text" 
                                                v-if="item.comment_editable" 
                                                :data-id="item.r_id"
                                                :ref="setItemRef"
                                                v-model="item.r_comment"
                                                @blur="enableComment(item.r_id, false)"
                                            >
                                        </td>

                                        <!-- 操作 -->
                                        <td>
                                            <!-- 編輯備註 -->
                                            <button @click="enableComment(item.r_id, true)" class="icon-btn edit"></button>

                                            <!-- 刪除紀錄 -->
                                            <button @click="removeRecord(item.r_id)" :disabled="item.del_btn_enable" class="icon-btn del"></button>

                                            <!-- 複製按鈕 -->
                                            <button @click="copyTrData($event)" class="icon-btn copy"></button>
                                        </td>
                                    </tr>

                                </tbody>
                            </table>
                        </div>

                    </div>
                </div>
            </div>
        </div>

    </section>


</template>

<script>
import { ref, toRef, onBeforeUpdate, onUpdated, nextTick } from 'vue';

export default {
    name: 'WorkingHoursCalculationBody',
    props: ['data'],
    emits: [
        'eventEnableComment',
        'eventRemoveRecord',
        'eventCopyTrData',
    ],

    setup(props, context) {
        // 從 props 來的資料
        const initData = toRef(props, 'data');

        // 元素
        let recordTable = ref(null)

        // 獲取 v-for 產生的動態元素
        // Vue.js ref inside the v-for loop
        // https://stackoverflow.com/questions/52086128/vue-js-ref-inside-the-v-for-loop
        let itemRefs = []
        const setItemRef = function(el) {
            if (el) {
                itemRefs.push({
                    id: el.getAttribute("data-id"),
                    el
                })
            }
        }
        onBeforeUpdate(() => {
            itemRefs = []
        })
        onUpdated(() => {
            // console.log(itemRefs)
        })


        // 格式化表格使用的文字
        function formatTableData(obj, type) {
            // [yyyy, mm, dd]
            let start_d_arr = obj.r_start ? (obj.r_start.split(' ')[0]).split('-') : undefined;
            // let end_d_arr = obj.r_end ? (obj.r_end.split(' ')[0]).split('-') : undefined;
            
            // [hh, mm, ss]
            let start_t_arr = obj.r_start ? (obj.r_start.split(' ')[1]).split(':') : undefined;
            let end_t_arr = obj.r_end ? (obj.r_end.split(' ')[1]).split(':') : undefined;

            // hs
            let origin_hs = obj.r_end ? ( ( (new Date(obj.r_end)).getTime() - (new Date(obj.r_start)).getTime() ) / 1000 / 60 / 60 ).toFixed(2) : undefined; // 原始
            let magic_hs = origin_hs ? (Math.ceil(origin_hs * 4) / 4).toFixed(2) : origin_hs; // 無條件進位
            let magic_hs_check = (magic_hs - origin_hs) <= 0.167 ? magic_hs : origin_hs; // 10分鐘以內的誤差才進位

            switch(type) {
                // 開始日期
                case 'start-date':
                    // 日期 (yyyy-mm-dd)
                    // return  obj.r_start ? obj.r_start.split(' ')[0] : '';

                    // 日期 (mm/dd)
                    return  obj.r_start ? `${start_d_arr[1]}/${start_d_arr[2]}` : '';

                // 開始時段
                case 'start-time':
                    // 時段 (hh:mm:ss)
                    // return  obj.r_start ? obj.r_start.split(' ')[1] : '';

                    // 時段 (hh:mm)
                    return  obj.r_start ? `${start_t_arr[0]}:${start_t_arr[1]}` : '';

                // 結束時段
                case 'end-time':
                    // 時段 (hh:mm:ss)
                    // return obj.r_end ? obj.r_end.split(' ')[1] : '';

                    // 時段 (hh:mm)
                    return obj.r_end ? `${end_t_arr[0]}:${end_t_arr[1]}` : '';

                // 真實時數
                case 'real-hours':
                    return origin_hs ? origin_hs : '-';

                // 魔法時數
                case 'magic-hours':
                    return  magic_hs_check ? magic_hs_check : '-';
            }
        }

        // 刪除紀錄
        function removeRecord(id) {
            context.emit('eventRemoveRecord', id);
        }

        // 開啟 | 關閉 備註欄位
        function enableComment(id, flag) {
            context.emit('eventEnableComment', id, flag);

            nextTick(()=>{
                // do stuff with the DOM

                // 關閉編輯模式
                if ( !flag ) return;

                // 尋找對應的 input 元素
                itemRefs.forEach((item) => {
                    if ( item.id == id ) {
                        item.el.focus(); // 讓 input 取得焦點
                    }
                });
            })
        }

        // 複製資料
        function copyTrData(e) {
            context.emit('eventCopyTrData', e);
        }


        return {
            initData,

            recordTable,
            setItemRef,

            formatTableData,
            removeRecord,
            enableComment,
            copyTrData,
        }
    }
}
</script>